<template>
  <div>
    <form
      class="block text-center p-4 font-poppins"
      @submit.prevent="sendMessage"
      v-show="!emailSent"
    >
      <div
        class="w-6/12 xs:w-8/12 flex m-auto space-x-6 rounded-lg border border-black p-3 mt-4 overflow-auto"
      >
        <img src="../../assets/images/person.svg" alt="name-icon" />
        <input
          class="placeholder-gray-500 placeholder-opacity-50 focus:border-0 w-full focus:outline-none"
          type="text"
          name="name"
          id="name"
          placeholder="Full Name"
          v-model="name"
        />
      </div>

      <div
        class="w-6/12 xs:w-8/12 flex m-auto space-x-6 rounded-lg border border-black p-3 mt-4 overflow-auto"
      >
        <img src="../../assets/images/envelope.svg" alt="email-icon" />
        <input
          class="placeholder-gray-500 placeholder-opacity-50 focus:border-0 w-full focus:outline-none"
          type="email"
          name="email"
          placeholder="Email Address"
          v-model="email"
        />
      </div>

      <div
        class="w-6/12 xs:w-8/12 flex items-start m-auto space-x-6 rounded-lg border border-black p-3 mt-4"
      >
        <img src="../../assets/images/pen.svg" alt="" />
        <textarea
          class="h-64 text-gray-500 w-full placeholder-gray-500 placeholder-opacity-50 focus:outline-none"
          placeholder="Message"
          v-model="message"
        ></textarea>
      </div>

      <input
        type="submit"
        value="send"
        class="mt-20 rounded-full text-white bg-swizel-colorB p-4 w-48 m-auto focus:outline-none"
      />
    </form>

    <EmailSent v-show="emailSent" />
  </div>
</template>

<script>
import axios from "axios";
import EmailSent from "./EmailSent";
export default {
  name: "contactForm",
  components: {
    EmailSent
  },
  data() {
    return {
      name: null,
      email: null,
      message: null,
      emailSent: false
    };
  },
  methods: {
    sendMessage: async function(e) {
      e.preventDefault();
      let data = {
        from: this.email,
        subject: "From Swizel.co Contact Page",
        message: this.message
      };

      try {
        let response = await axios.post(
          "https://swizel-email-api.herokuapp.com/v1/emails/send",
          data
        );

        if (response.status == 200) {
          this.emailSent = true;

          setTimeout(() => {
            this.name = null;
            this.email = null;
            this.message = null;
            this.emailSent = false;
          }, 6000);
        }
      } catch (error) {
        alert("Somethig went wrong, please try again");
        this.name = null;
        this.email = null;
        this.message = null;
      }
    }
  }
};
</script>
